@import url(https://fonts.googleapis.com/css2?family=Prompt:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Prompt",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  color: #2b3876;
}

h6 {
  font-family: "Prompt";
  color: gray;
}

.squareOpen {
  height: 20px;
  width: 50px;
  background-color: #5dc239;
}

.squareClose {
  height: 20px;
  width: 50px;
  background-color: #a0a0a0;
}
.circle {
  height: 50px;
  width: 50px;
  background-color: #555;
  border-radius: 50%;
}
.styleImgCar {
  width: "6%";
  height: "6%";
  margin: "0.5%";
  padding: "0.5%";
  backgroundcolor: #5dc239;
}
.styleImgRes {
  width: "5%";
  height: "5%";
  margin: "0.5%";
  padding: "0.5%";
  backgroundcolor: #5dc239;
}

